import {
    CALCULATION_LOAD_ERROR,
    CALCULATION_LOAD_START,
    CALCULATION_LOADED
} from './action-list';

import * as calculationResources from '../resources/calculation';

export function loadCalculation(payload) {
    return async function (dispatch) {
        dispatch({
            type: CALCULATION_LOAD_START
        });
        
        return await calculationResources.getCalculation(payload)
            .then((response) => {
                console.log(response);
            })
            .catch((error) => {
                throw error;
            });
    }
} 



